import React from "react"

import Layout from "../components/layout"
import SEO from "../components/seo"
import Image from "../components/image"
import Grid from "../components/grid"

import styled from "styled-components"

const RoleDescription = styled.p`
  font-style: italic;
`

const TeamItem = styled.div`
  min-width: 200px;
  padding: 2em 1em;
  // transition: background-color 0.4s;
  :hover {
    // background-color: lightgray;

    img {
      transform: scale(1.2);
    }
  }

  a {
    color: gray;
  }
`

// const persons = [...Array(2)].map((item, index) => ({ img: "", index }))

const TeamPage = ({ data }) => {
  console.log("PRODUCT INDEX, data:", data)

  const members = data.allMember.edges

  return (
    <Layout>
      <SEO title="Team" />
      <h1>TEAM</h1>
      <p>
        Fusce ac blandit massa. Mauris cursus imperdiet vulputate. Nam dictum,
        tellus quis lobortis blandit, mauris nunc lobortis quam, quis
        scelerisque eros augue in magna. Nulla convallis erat urna, fringilla
        auctor augue dictum vitae. Aenean fermentum consequat commodo. Curabitur
        euismod gravida purus fermentum vehicula. Nulla at justo sed lorem
        aliquet hendrerit a ut ex.{" "}
      </p>

      <Grid
        items={members}
        render={({ item }) => (
          <TeamItem>
            <h3>{item.node.name}</h3>
            {/* <h3>{`Person ID=${item.node.memberId}`}</h3> */}
            <Image name={item.node.photo} />
            <RoleDescription>{item.node.title}</RoleDescription>
          </TeamItem>
        )}
      ></Grid>
    </Layout>
  )
}

export const query = graphql`
  query {
    allMember(sort: { fields: name, order: ASC }) {
      edges {
        node {
          name
          memberId
          title
          photo
        }
      }
    }
  }
`

export default TeamPage
